@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+39&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+39&family=Libre+Barcode+39+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  
  width: 100%;
  height:100%;
}

body::-webkit-scrollbar {
  display: none;
}

body {
  scroll-behavior: smooth;
    background: linear-gradient(-45deg, #f8ccc0, #eea2be, #FFAEAE);
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

h4{
  margin-left: 15px;
  margin-bottom: 0%;
  margin-top: 5px;
}

.custom-shape-divider-top-1634922272 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1634922272 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 126px;
}

.custom-shape-divider-top-1634922272 .shape-fill {
  fill: #FFFFFF;
}

.enter{
  color: white;
  font-weight: bold;
  margin-top: 70px;
  cursor: pointer;
  padding: 5px 30px;
  /* border: solid rgb(151, 56, 56) 2px; */
  background-color: rgb(170, 70, 87)
}
.header{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
  }
  .header h1{
    font-family: 'Jost', sans-serif;
    text-shadow: 0px 2px 3px #DB7F7F;
    margin-bottom: 0px;
    letter-spacing: 2px;
  }
  .description{
    margin-top: 5px;
  }

  .intro{
    color:white;
    text-align: 'left';
    }
    
.project-card{
    display: flex;
    margin: 20px;
    transition: 0.3s;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    background-color:white;
    width: 50vh;
    border-radius: 3%;
  }

  .project-img {
    width: 30%;
    height: 100%;
    object-fit: cover;
    object-position: 20% 50%;
    border-top-left-radius: 2%;
    border-bottom-left-radius: 2%;
  }

  .project-card > div > .project-title{
    font-size: 30px;
    text-align: center;
    font-weight: bold;
  }
  .project-card:hover{
    transform: scale(1.05);
  }
  
  .desc{
    position: relative;
    height: 80px;
    padding: 15px;
  }
  
  .card-footer {
    width: 20%;
    bottom: 10px;
  }

  .github-logo{
    padding: 0px 0px 5px 10px;
  }
  
.sidebar {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  position: fixed;
  width: 100%;
  height: 100%;
}

button {
  outline: none;
  border: none;
}

.home-button,
.projects-button {
  margin: 5px;
  margin-right: 30px;
  border-radius: 100%;
  padding: 10px;
}

.home-group,
.projects-group {
  display: flex;
  flex-direction: row-reverse;
}

.home-sidebar,
.projects-sidebar {
  display: none;
  font-size: 20px;
  color: white;
  margin-right: 10px;
}

.home-button:hover + .home-sidebar {
  display: block;
  animation-name: fade-in;
  animation-duration: 0.4s;
}

.projects-button:hover + .projects-sidebar {
  display: block;
  animation-name: fade-in;
  animation-duration: 0.4s;
}

.home-button:hover,
.projects-button:hover {
  cursor: pointer;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100%;
  }
}

.projects{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  
  .project-page-header{
    padding-top:100px;
    margin-bottom: 0px;
    letter-spacing: 2px;
  }
  
  .projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2%;
  }
  
  .project-page{
    display:flex;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .project-page h1{
    color:white;
    font-family: 'Jost', sans-serif;
    text-shadow: 0px 2px 3px #DB7F7F;
    font-size: 60px;
    padding-top: 0;
  }
  
  
  @keyframes onHover{
    0%{
      opacity: 0px;
    }
    100%{
      opacity: 100%;
    }
  }

  @media screen and (min-width: 1240px){
      .projects-container{
          padding-right: 10%;
          padding-left: 10%;
      }
  }
